import React, { Component , Fragment } from 'react'
import styled from 'styled-components';
import Card from './Card'
import quality from './Assets/quilty.svg'
import exp from './Assets/Experinces.svg'
import time from './Assets/Time.svg'
const Aboutus = styled.section`
margin-top:10%;
height: 40vh;
width:100%;
background-color:transparent;

`
const Contain = styled.div`
margin-left:-200px;
`
const qual = 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Cumque hic ipsam mollitia nulla, blanditiis quasi earum reprehenderit? Reiciendis, numquam repudiandae?';
const exper = 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Cumque hic ipsam mollitia nulla, blanditiis quasi earum reprehenderit? Reiciendis, numquam repudiandae?';
const tim = 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Cumque hic ipsam mollitia nulla, blanditiis quasi earum reprehenderit? Reiciendis, numquam repudiandae?';

export default class AboutUs extends Component {
  render() {
    return (
     <Fragment>
        <Aboutus>
             <Contain>
           <div className="container">
           <div className="row">
           <div className="col-md-4">
           
           <Card 
           image={quality}
           title="Quality" desc={qual}/>
           </div>
           <div className="col-md-4">
           <Card
           image={exp}
           title="Experince" desc={exper}/>
           </div>
           <div className="col-md-4">
           <Card 
           image={time}
           title="Time" 
           desc={tim}/>
           </div>
           
           </div>
           </div>
            
           </Contain>

        </Aboutus>

     </Fragment>
    )
  }
}



