import React, { Component } from 'react'
import styled from 'styled-components'
import {Clading} from '../data'
import Project from '../project'
import Box from '../Box'

 



const Pointer= styled.div`
cursor:pointer;
`
const Post = styled.div`

`

export default class CladingFrame extends Component {
constructor(props){
super(props);
this.state={
 data: Clading,
 projectmode:false,
 projectImages:[]

}

}

getProject= (event)=>{
  const didProject = Clading[event].images
  const OldImages = this.state.projectImages
  OldImages.splice(0,OldImages.length)
  OldImages.push(didProject)
this.setState({
  projectmode:true,
  projectImages: OldImages
})

}
close = (event)=>{
  const did =event
  this.setState({
    projectmode : did
  })

 }
 render() {
 
 

    return (
        
          <div className="container">
          <div className="row">
          
         {
        this.state.projectmode ?<Box open={this.state.projectmode} images={this.state.projectImages} close={this.close}/>

          
           :this.state.data.map((project , i) =>
           <Post className="col-md-3 pointer animated fadeInUp">
           <Pointer key={i} onClick={()=>this.getProject(i)}>
          <Project name={project.name} image={project.thumb} />
          </Pointer>
          </Post>
          )
         
         }
            </div>
            </div>
        
    )
  }
}



