import React, { Component , Fragment } from 'react'
import './Card.css'




export default class Card extends Component {
  render() {

    const {image , title , desc} = this.props

    return (
   <Fragment>
    <div className="card text-center animated fadeInDown" style={{width: '30rem', height: '25rem'}}>
    <img className="card-img-top" src={image} alt="Card image cap" width={200} height={200}/>
    <div className="card-body">
        <h5 className="card-title">{title}</h5>
        <p className="card-text">{desc}</p>
    </div>
    </div>
    </Fragment>
    )
  }
}



