import React, { Component } from 'react'
import './Content.css'
import billboard from './Assets/billboard.svg';

export default class Content extends Component {
  render() {
    return (
<div className="container">
  <div className="row">
    <div id="data" className="col-md-6">
      <div className="slide">
        <p className="item-1">Lorem ipsum dolor sit amet consectetur adipisicing elit. Incidunt, quidem?</p>
        <p className="item-2">Lorem ipsum dolor sit amet consectetur adipisicing elit. Deleniti, voluptatibus!</p>
        <p className="item-3">Lorem ipsum dolor sit amet, consectetur adipisicing elit. In, iure?</p>
      </div>
      <div className="flex animated fadeIn">
        <button> Take A Tour </button>
      </div>
    </div>
    <div id="svg" className="col-md-3 animated fadeInRight ">
      <div className="cont">
        <img className src={billboard} width={450} height="auto" alt />
      </div>
    </div>
  </div>
</div>

    )
  }
}



