import React, { Component, Fragment } from 'react'
import styled from 'styled-components'
import CladingFrame from './Frames/clading' 
import Aclerik from './Frames/aclerik' 
import Scroll from './Scroll'
import {BrowserRouter , Route} from 'react-router-dom'


import List from './List'
const Main = styled.div`
margin-top:5%;

`
export default class Work extends Component {
 constructor(){
super();
this.state={
cat :'aclerik'

}


 }

 swap = (event)=>{
   const did =event
   this.setState({
     cat : did
   })
 
  }
  render() {
    // const result = data.filter(dat =>{
    //   return dat.category.toLowerCase().includes(this.state.cat)
    //   });
    console.log(this.state.cat)
    return (
      <BrowserRouter>
      <div>
      <center>
     <Main>
    <div className="container-fluid">
    <div >
    <div className="col-md-12">
    <List cat={this.swap}/>
    </div>
    <div className="col-md-12">
    {/* <Route exact path="/work"  component={Clading}/> */}
    <Route  path="/work/clading"  component={CladingFrame}/>
    <Route  path="/work/aclerick"  component={Aclerik}/>


    </div>
    </div>
    </div>

     </Main>
     </center>
     </div>
     </BrowserRouter>
      
    )
  }
}



