import React, { Component, Fragment } from 'react'
import'./Navbar.css';
import logo from'./Assets/logo.svg'
import styled, { keyframes } from 'styled-components';
import { Link } from 'react-router-dom'  
const Flex = styled.div`
display:flex;
flex-direction:row;

flex-basis:500px;
  `



const fadeIn = keyframes`
0%{opacity : 0; }
100%{opacity : 1; }
`;

const fadeOut = keyframes`
0%{opacity : 1; }
100%{opacity : 0; }
`;




const Ul =styled.ul`
               
           visibility: ${props => props.hide ? "hidden" : "visible"};
           display:block;
           width:70%;
           list-style: none;
           margin-left: 40%;
           padding-top: 5px; 
           animation-name:${props => props.hide ? fadeOut : fadeIn} 9s ease;
            overflow: hidden;       
`


const StLink = styled(Link)`
 font-size: 0.9em;
    display: block;
    color:white !important;
    font-weight: 400;
    padding:5px 10px;
    margin: 0 15px;
    text-decoration: none !important; 
    border: #fff solid 1px;
    border-radius:2px;
    transition:0.9s ease;
    &:hover{
      text-decoration:none;
      color: #27b9d2 !important;
      background-color:white;
    }
`
const Li =styled.li`

float: left;
`
const A =styled.a`

    font-size: 0.9em;
    display: block;
    color:white !important;
    font-weight: 400;
    padding:5px 10px;
    margin: 0 15px;
    text-decoration: none; 
    border: #fff solid 1px;
    border-radius:2px;
`

const Icon = styled.i`
font-size:3em;
margin-left:30%;
color:white;
text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1), ;
transition: 0.9s;
transform:${p => p.rotate?"rotate(90deg)":"rotate(0deg)"} ;
cursor:pointer;
`
const Contain1 =styled.div`

flex:80%;
`
const Contain2 =styled.div`

flex:20%;
flex-basis:20%;
`



export default class list extends Component {
constructor(props){
super(props)
this.state={
    hide:true,
    rotate:false
}

}

handleclick =()=>{
this.setState({
    hide: !this.state.hide,
    rotate: !this.state.rotate
})
}

  render() {
    return (
      <Fragment>
<Flex>
  <Contain1>
  <Ul hide={this.state.hide}>
      <Li> <StLink to='/'>Home</StLink></Li>
      <Li> <StLink to='/about'> About</StLink></Li>
      <Li> <StLink to='/work/clading'> Our Work</StLink></Li>
      <Li> <StLink to='/contact'>Contact</StLink></Li>
    
    </Ul>
    </Contain1>
   <Contain2>
    <Icon rotate={this.state.rotate} onClick={this.handleclick} className="fas fa-chevron-circle-down"></Icon>
    </Contain2>
    </Flex>
      </Fragment>
    )
  }
}



